<template>
  <div class="tag-view">
    <div class="page-header">
      <h2>标签管理</h2>
      <el-button type="primary" @click="$router.push('/posts/create')">
        写文章
      </el-button>
    </div>

    <div class="tag-stats">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-card class="stat-card">
            <template #header>
              <div class="card-header">
                <span>总标签数</span>
              </div>
            </template>
            <div class="stat-value">{{ totalTags }}</div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card class="stat-card">
            <template #header>
              <div class="card-header">
                <span>已使用标签</span>
              </div>
            </template>
            <div class="stat-value">{{ usedTags }}</div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card class="stat-card">
            <template #header>
              <div class="card-header">
                <span>未使用标签</span>
              </div>
            </template>
            <div class="stat-value">{{ unusedTags }}</div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <div class="tag-management">
      <el-card>
        <template #header>
          <div class="card-header">
            <span>标签列表</span>
            <el-input
              v-model="searchQuery"
              placeholder="搜索标签..."
              class="tag-search"
              clearable
            >
              <template #prefix>
                <el-icon><Search /></el-icon>
              </template>
            </el-input>
          </div>
        </template>
        
        <tag-list
          v-model="tags"
          @add="handleAddTag"
          @delete="handleDeleteTag"
        />
      </el-card>
    </div>

    <div class="tag-analysis">
      <el-card>
        <template #header>
          <div class="card-header">
            <span>标签使用分析</span>
          </div>
        </template>
        <div class="analysis-content">
          <div class="chart-container">
            <!-- 这里可以添加图表组件，如 ECharts -->
            <div class="placeholder-chart">
              标签使用频率图表
            </div>
          </div>
          <div class="popular-tags">
            <h4>热门标签</h4>
            <ul>
              <li v-for="tag in popularTags" :key="tag.id">
                <span class="tag-name">{{ tag.name }}</span>
                <span class="tag-usage">{{ tag.count }} 篇文章</span>
              </li>
            </ul>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import TagList from '@/components/posts/TagList.vue'

const searchQuery = ref('')
const tags = ref([
  { id: 1, name: 'Vue3', type: 'primary', count: 15 },
  { id: 2, name: 'JavaScript', type: 'success', count: 12 },
  { id: 3, name: 'CSS', type: 'warning', count: 8 },
  { id: 4, name: '前端开发', type: 'info', count: 10 },
  { id: 5, name: '性能优化', type: 'danger', count: 6 }
])

// 统计数据
const totalTags = computed(() => tags.value.length)
const usedTags = computed(() => tags.value.filter(tag => tag.count > 0).length)
const unusedTags = computed(() => totalTags.value - usedTags.value)

// 热门标签
const popularTags = computed(() => {
  return [...tags.value]
    .sort((a, b) => b.count - a.count)
    .slice(0, 5)
})

const handleAddTag = (tag) => {
  tags.value.push(tag)
}

const handleDeleteTag = (tag) => {
  const index = tags.value.findIndex(t => t.id === tag.id)
  if (index !== -1) {
    tags.value.splice(index, 1)
  }
}
</script>

