<template>
  <div class="post-detail">
    <div class="post-header">
      <h1>{{ post.title }}</h1>
      <div class="post-meta">
        <span class="post-date">
          <i class="el-icon-date"></i>
          {{ formatDate(post.date) }}
        </span>
        <span class="post-category">
          <i class="el-icon-collection-tag"></i>
          {{ post.category }}
        </span>
        <span class="post-views">
          <i class="el-icon-view"></i>
          {{ post.views }} 阅读
        </span>
      </div>
      <div class="post-tags">
        <el-tag v-for="tag in post.tags" :key="tag" size="small" effect="plain">
          {{ tag }}
        </el-tag>
      </div>
    </div>

    <div class="post-content" v-html="post.content"></div>

    <div class="post-footer">
      <div class="post-navigation">
        <div v-if="post.prevPost" class="prev-post">
          <span>上一篇</span>
          <router-link :to="`/posts/${post.prevPost.id}`">
            {{ post.prevPost.title }}
          </router-link>
        </div>
        <div v-if="post.nextPost" class="next-post">
          <span>下一篇</span>
          <router-link :to="`/posts/${post.nextPost.id}`">
            {{ post.nextPost.title }}
          </router-link>
        </div>
      </div>

      <div class="post-actions">
        <el-button 
          :type="hasLiked ? 'success' : 'primary'" 
          @click="handleLike"
          :loading="likeLoading"
        >
          <i :class="['el-icon-star-off', { 'liked': hasLiked }]"></i>
          {{ hasLiked ? '已点赞' : '点赞' }} ({{ post.likes }})
        </el-button>
        <el-button @click="sharePost">
          <i class="el-icon-share"></i>
          分享
        </el-button>
      </div>
    </div>

    <!-- 添加评论部分 -->
    <div class="comments-section">
      <h3>评论区</h3>
      
      <!-- 评论输入框 -->
      <div class="comment-input">
        <el-input
          v-model="commentContent"
          type="textarea"
          :rows="3"
          placeholder="写下你的评论..."
          :maxlength="500"
          show-word-limit
        />
        <div class="comment-actions">
          <el-button type="primary" @click="submitComment" :loading="submitting">
            发表评论
          </el-button>
        </div>
      </div>

      <!-- 评论列表 -->
      <div class="comments-list">
        <template v-if="localComments.length">
          <div v-for="comment in localComments" :key="comment.id" class="comment-item">
            <div class="comment-content">
              <p>{{ comment.content }}</p>
              <div class="comment-info">
                <span class="comment-time">{{ formatCommentTime(comment.time) }}</span>
                <el-button 
                  type="danger" 
                  size="small" 
                  link 
                  @click="deleteComment(comment.id)"
                >
                  删除
                </el-button>
              </div>
            </div>
          </div>
        </template>
        <el-empty v-else description="暂无评论" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { getPostById, getAdjacentPosts } from '@/data/posts'
import { ElMessage, ElMessageBox } from 'element-plus'

const route = useRoute()
const router = useRouter()

const formatDate = (date) => {
  return new Date(date).toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })
}

// 获取文章数据
const post = computed(() => {
  const currentPost = getPostById(route.params.id)
  if (!currentPost) {
    router.push({ name: 'not-found' })
    return null
  }
  return currentPost
})

// 获取相邻文章
const { prev: prevPost, next: nextPost } = computed(() => 
  getAdjacentPosts(route.params.id)
).value

// 点赞相关状态
const likeLoading = ref(false)
const hasLiked = ref(false)

// 评论相关状态
const commentContent = ref('')
const submitting = ref(false)
const localComments = ref([])

// 从本地存储获取点赞状态
onMounted(() => {
  if (post.value) {
    // 增加访问量
    post.value.views++
    
    // 获取点赞状态
    const likedPosts = JSON.parse(localStorage.getItem('likedPosts') || '[]')
    hasLiked.value = likedPosts.includes(post.value.id)

    // 加载评论
    const savedComments = JSON.parse(
      localStorage.getItem(`post_${post.value.id}_comments`) || '[]'
    )
    localComments.value = savedComments
  }
})

// 处理点赞
const handleLike = async () => {
  if (!post.value) return
  
  likeLoading.value = true
  
  try {
    // 获取已点赞文章列表
    const likedPosts = JSON.parse(localStorage.getItem('likedPosts') || '[]')
    
    if (hasLiked.value) {
      // 取消点赞
      post.value.likes--
      const index = likedPosts.indexOf(post.value.id)
      if (index > -1) {
        likedPosts.splice(index, 1)
      }
      hasLiked.value = false
      ElMessage.success('已取消点赞')
    } else {
      // 添加点赞
      post.value.likes++
      likedPosts.push(post.value.id)
      hasLiked.value = true
      ElMessage.success('点赞成功')
    }
    
    // 保存点赞状态到本地存储
    localStorage.setItem('likedPosts', JSON.stringify(likedPosts))
    
  } catch (error) {
    ElMessage.error('操作失败，请稍后重试')
  } finally {
    likeLoading.value = false
  }
}

const sharePost = () => {
  ElMessage.success('分享功能开发中...')
}

// 提交评论
const submitComment = () => {
  if (!commentContent.value.trim()) return
  
  submitting.value = true
  
  try {
    const newComment = {
      id: Date.now(),
      content: commentContent.value.trim(),
      time: new Date().toISOString()
    }
    
    // 添加到评论列表
    localComments.value.unshift(newComment)
    
    // 保存到本地存储
    localStorage.setItem(
      `post_${post.value.id}_comments`,
      JSON.stringify(localComments.value)
    )
    
    // 清空输入框
    commentContent.value = ''
    
    ElMessage.success('评论发表成功')
  } catch (error) {
    ElMessage.error('评论发表失败')
  } finally {
    submitting.value = false
  }
}

// 删除评论
const deleteComment = (commentId) => {
  ElMessageBox.confirm(
    '确定要删除这条评论吗？',
    '提示',
    {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }
  ).then(() => {
    const index = localComments.value.findIndex(c => c.id === commentId)
    if (index > -1) {
      localComments.value.splice(index, 1)
      
      // 更新本地存储
      localStorage.setItem(
        `post_${post.value.id}_comments`,
        JSON.stringify(localComments.value)
      )
      
      ElMessage.success('评论已删除')
    }
  }).catch(() => {})
}

// 格式化评论时间
const formatCommentTime = (time) => {
  const date = new Date(time)
  const now = new Date()
  const diff = now - date

  // 小于1分钟
  if (diff < 60000) {
    return '刚刚'
  }
  // 小于1小时
  if (diff < 3600000) {
    return `${Math.floor(diff / 60000)}分钟前`
  }
  // 小于24小时
  if (diff < 86400000) {
    return `${Math.floor(diff / 3600000)}小时前`
  }
  // 小于30天
  if (diff < 2592000000) {
    return `${Math.floor(diff / 86400000)}天前`
  }
  // 其他情况返回完整日期
  return date.toLocaleDateString('zh-CN', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  })
}
</script>

