<template>
    <div class="add-post">
        <div class="page-header">
            <h2>添加文章</h2>
            <div class="actions">
                <el-button @click="$router.push('/posts')">
                    返回文章列表
                </el-button>
            </div>
        </div>

        <!-- 添加文章表单 -->
        <el-form :model="newPost" label-width="100px" class="post-form">
            <el-card class="form-section">
                <template #header>
                    <div class="section-header">
                        <span>基本信息</span>
                    </div>
                </template>

                <el-form-item label="文章标题" required>
                    <el-input v-model="newPost.title" placeholder="请输入文章标题" />
                </el-form-item>

                <el-form-item label="文章分类" required>
                    <el-select v-model="newPost.category" placeholder="请选择分类">
                        <el-option v-for="category in categories" :key="category" :label="category" :value="category" />
                    </el-select>
                </el-form-item>

                <el-form-item label="文章标签" required>
                    <el-select v-model="newPost.tags" multiple filterable allow-create default-first-option
                        placeholder="请选择或创建标签">
                        <el-option v-for="tag in availableTags" :key="tag" :label="tag" :value="tag" />
                    </el-select>
                </el-form-item>

                <el-form-item label="文章摘要" required>
                    <el-input v-model="newPost.excerpt" type="textarea" :rows="3" placeholder="请输入文章摘要" />
                </el-form-item>
            </el-card>

            <el-card class="form-section">
                <template #header>
                    <div class="section-header">
                        <span>文章内容</span>
                        <el-tooltip content="支持HTML标签" placement="top">
                            <i class="el-icon-question"></i>
                        </el-tooltip>
                    </div>
                </template>

                <div class="content-sections">
                    <div class="section-item">
                        <div class="section-title">引言</div>
                        <el-input v-model="content.intro" type="textarea" :rows="4" placeholder="请输入文章引言" />
                    </div>

                    <div class="section-item">
                        <div class="section-title">1. 基础概念</div>
                        <el-input v-model="content.basics" type="textarea" :rows="4" placeholder="请输入基础概念部分" />
                        <el-input v-model="content.basicsList" type="textarea" :rows="4" placeholder="请输入要点列表，每行一个要点" />
                    </div>

                    <div class="section-item">
                        <div class="section-title">2. 实践应用</div>
                        <el-input v-model="content.practice" type="textarea" :rows="4" placeholder="请输入实践应用部分" />
                        <el-input v-model="content.practiceList" type="textarea" :rows="4"
                            placeholder="请输入要点列表，每行一个要点" />
                    </div>

                    <div class="section-item">
                        <div class="section-title">3. 最佳实践</div>
                        <el-input v-model="content.bestPractices" type="textarea" :rows="4" placeholder="请输入最佳实践部分" />
                        <el-input v-model="content.bestPracticesList" type="textarea" :rows="4"
                            placeholder="请输入要点列表，每行一个要点" />
                    </div>

                    <div class="section-item">
                        <div class="section-title">总结</div>
                        <el-input v-model="content.summary" type="textarea" :rows="4" placeholder="请输入文章总结" />
                    </div>
                </div>
            </el-card>

            <div class="form-actions">
                <el-button type="primary" size="large" @click="submitPost">
                    发布文章
                </el-button>
            </div>
        </el-form>
    </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { postsDatabase } from '@/data/posts'

const router = useRouter()

// 分类选项
const categories = [
    '前端', '后端', '工具', '架构', '教程',
    '技术', '网络', '安全', '数据库', '运维'
]

// 可用的标签
const availableTags = [
    'Vue3', 'JavaScript', 'TypeScript', 'React', 'Node.js',
    'CSS', 'HTML', 'Web开发', '性能优化', '前端框架'
]

// 新文章表单数据
const newPost = ref({
    title: '',
    category: '',
    tags: [],
    excerpt: ''
})

// 文章内容各部分
const content = ref({
    intro: '',
    basics: '',
    basicsList: '',
    practice: '',
    practiceList: '',
    bestPractices: '',
    bestPracticesList: '',
    summary: ''
})

// 生成HTML内容
const generateContent = () => {
    const basicsList = content.value.basicsList.split('\n').filter(item => item.trim())
    const practiceList = content.value.practiceList.split('\n').filter(item => item.trim())
    const bestPracticesList = content.value.bestPracticesList.split('\n').filter(item => item.trim())

    return `
    <h2>引言</h2>
    <p>${content.value.intro}</p>
    
    <h2>1. 基础概念</h2>
    <p>${content.value.basics}</p>
    <ul>
      ${basicsList.map(item => `<li>${item}</li>`).join('\n')}
    </ul>

    <h2>2. 实践应用</h2>
    <p>${content.value.practice}</p>
    <ul>
      ${practiceList.map(item => `<li>${item}</li>`).join('\n')}
    </ul>

    <h2>3. 最佳实践</h2>
    <p>${content.value.bestPractices}</p>
    <ul>
      ${bestPracticesList.map(item => `<li>${item}</li>`).join('\n')}
    </ul>

    <h2>总结</h2>
    <p>${content.value.summary}</p>
  `
}

// 提交文章
const submitPost = () => {
    // 表单验证
    if (!newPost.value.title || !newPost.value.category ||
        !newPost.value.tags.length || !newPost.value.excerpt) {
        ElMessage.warning('请填写所有必填字段')
        return
    }

    // 创建新文章对象
    const post = {
        id: postsDatabase.length + 1,
        title: newPost.value.title,
        date: new Date().toISOString(),
        category: newPost.value.category,
        views: 0,
        likes: 0,
        tags: newPost.value.tags,
        excerpt: newPost.value.excerpt,
        content: generateContent(),
        comments: []
    }

    // 添加到文章数据库
    postsDatabase.push(post)

    ElMessage.success('文章发布成功')
    router.push('/posts')
}
</script>

