<template>
  <div class="post-create">
    <div class="page-header">
      <h2>创建文章</h2>
      <div class="actions">
        <el-button @click="saveDraft">保存草稿</el-button>
        <el-button type="primary" @click="publish">发布文章</el-button>
      </div>
    </div>

    <el-form :model="post" label-width="100px" class="post-form">
      <el-form-item label="文章标题" required>
        <el-input v-model="post.title" placeholder="请输入文章标题" />
      </el-form-item>

      <el-form-item label="封面图片">
        <el-upload
          class="cover-uploader"
          action="/api/upload"
          :show-file-list="false"
          :on-success="handleCoverSuccess"
        >
          <img v-if="post.cover" :src="post.cover" class="cover-image" />
          <el-icon v-else class="cover-uploader-icon"><Plus /></el-icon>
        </el-upload>
      </el-form-item>

      <el-form-item label="文章分类" required>
        <el-select v-model="post.category" placeholder="请选择分类">
          <el-option
            v-for="category in categories"
            :key="category.value"
            :label="category.label"
            :value="category.value"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="文章标签">
        <el-select
          v-model="post.tags"
          multiple
          filterable
          allow-create
          default-first-option
          placeholder="请选择或创建标签"
        >
          <el-option
            v-for="tag in availableTags"
            :key="tag.id"
            :label="tag.name"
            :value="tag.id"
          />
        </el-select>
      </el-form-item>

      <el-form-item label="文章摘要">
        <el-input
          v-model="post.excerpt"
          type="textarea"
          :rows="3"
          placeholder="请输入文章摘要"
        />
      </el-form-item>

      <el-form-item label="文章内容" required>
        <div class="editor-container">
          <!-- 这里可以集成 Markdown 编辑器，比如 md-editor-v3 -->
          <el-input
            v-model="post.content"
            type="textarea"
            :rows="15"
            placeholder="请输入文章内容"
          />
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { categories, createNewPost } from '@/utils/postSchema'

const router = useRouter()
const post = ref(createNewPost())

// 可用的标签列表
const availableTags = ref([
  { id: 1, name: 'Vue3' },
  { id: 2, name: 'JavaScript' },
  { id: 3, name: 'CSS' }
])

const handleCoverSuccess = (res) => {
  post.value.cover = res.url
}

const saveDraft = async () => {
  try {
    post.value.status = 'draft'
    // 这里添加保存草稿的逻辑
    ElMessage.success('草稿保存成功')
  } catch (error) {
    ElMessage.error('保存失败')
  }
}

const publish = async () => {
  try {
    post.value.status = 'published'
    // 这里添加发布文章的逻辑
    ElMessage.success('文章发布成功')
    router.push('/posts')
  } catch (error) {
    ElMessage.error('发布失败')
  }
}
</script>

