.post-create {
  padding: 20px;

  .page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;

    .actions {
      display: flex;
      gap: 10px;
    }
  }

  .post-form {
    max-width: 1000px;
    margin: 0 auto;

    .cover-uploader {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      width: 300px;
      height: 180px;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        border-color: #409EFF;
      }

      .cover-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .cover-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .editor-container {
      border: 1px solid #dcdfe6;
      border-radius: 4px;
    }
  }
}
</style> 