<template>
  <div class="home">
    <div class="hero-section">
      <h1>欢迎来到我的博客</h1>
      <p>分享技术、生活和有趣的故事</p>
      <div class="blog-overview">
        <div class="overview-item">
          <i class="el-icon-document"></i>
          <span>{{ totalPosts }} 篇文章</span>
        </div>
        <div class="overview-item">
          <i class="el-icon-view"></i>
          <span>{{ totalViews }} 次阅读</span>
        </div>
        <div class="overview-item">
          <i class="el-icon-chat-dot-round"></i>
          <span>{{ totalComments }} 条评论</span>
        </div>
      </div>
      <el-button type="primary" size="large" @click="$router.push('/posts')">
        浏览文章
      </el-button>
    </div>

    <div class="featured-posts">
      <h2>精选文章</h2>
      <div class="posts-grid">
        <el-card v-for="post in featuredPosts" :key="post.id" class="featured-post-card">
          <div class="post-header">
            <el-tag size="small" effect="plain">{{ post.category }}</el-tag>
            <div class="post-stats">
              <span class="post-views">
                <i class="el-icon-view"></i> {{ post.views }}
              </span>
              <span class="post-likes">
                <i class="el-icon-star-off"></i> {{ post.likes || 0 }}
              </span>
              <span class="post-comments">
                <i class="el-icon-chat-dot-round"></i> {{ post.commentsCount }}
              </span>
            </div>
          </div>
          <h3 @click="$router.push(`/posts/${post.id}`)">{{ post.title }}</h3>
          <p>{{ post.excerpt }}</p>
          <div class="post-footer">
            <div class="post-tags">
              <el-tag v-for="tag in post.tags.slice(0, 2)" :key="tag" size="small" effect="light">
                {{ tag }}
              </el-tag>
            </div>
            <el-button text @click="$router.push(`/posts/${post.id}`)">
              阅读更多
            </el-button>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed } from 'vue'
import { useRouter } from 'vue-router'
import { getPostsList } from '@/data/posts'

const router = useRouter()

// 获取所有文章数据
const allPosts = computed(() => getPostsList())

// 计算总文章数
const totalPosts = computed(() => allPosts.value.length)

// 计算总阅读量
const totalViews = computed(() => 
  allPosts.value.reduce((sum, post) => sum + post.views, 0)
)

// 计算总评论数（从localStorage获取）
const totalComments = computed(() => {
  return allPosts.value.reduce((sum, post) => {
    // 从 localStorage 获取每篇文章的评论
    const comments = JSON.parse(
      localStorage.getItem(`post_${post.id}_comments`) || '[]'
    )
    return sum + comments.length
  }, 0)
})

// 获取精选文章（按阅读量排序）
const featuredPosts = computed(() => {
  return [...allPosts.value]
    .map(post => {
      // 获取文章的评论数
      const comments = JSON.parse(
        localStorage.getItem(`post_${post.id}_comments`) || '[]'
      )
      return {
        ...post,
        commentsCount: comments.length
      }
    })
    .sort((a, b) => {
      // 首先按阅读量排序
      const viewsDiff = b.views - a.views
      if (viewsDiff !== 0) return viewsDiff
      
      // 阅读量相同时，按评论数排序
      const commentsDiff = b.commentsCount - a.commentsCount
      if (commentsDiff !== 0) return commentsDiff
      
      // 最后按发布日期排序
      return new Date(b.date) - new Date(a.date)
    })
    .slice(0, 3)
})
</script>

