// 计算是否有活动的筛选条件
const hasActiveFilters = computed(() => {
  return searchQuery.value || selectedCategory.value
})

// 获取分类标签
const getCategoryLabel = (value) => {
  const category = categories.value.find(c => c.value === value)
  return category ? category.label : value
}

// 搜索和筛选逻辑
const filteredPosts = computed(() => {
  let result = allPosts.value

  // 搜索过滤
  if (searchQuery.value) {
    const query = searchQuery.value.toLowerCase()
    result = result.filter(post => 
      post.title.toLowerCase().includes(query) ||
      post.excerpt.toLowerCase().includes(query) ||
      post.tags.some(tag => tag.toLowerCase().includes(query))
    )
  }

  // 分类过滤
  if (selectedCategory.value) {
    result = result.filter(post => post.category === selectedCategory.value)
  }

  // 排序
  result = [...result].sort((a, b) => {
    switch (sortBy.value) {
      case 'newest':
        return new Date(b.date) - new Date(a.date)
      case 'oldest':
        return new Date(a.date) - new Date(b.date)
      case 'most-viewed':
        return b.views - a.views
      case 'most-liked':
        return (b.likes || 0) - (a.likes || 0)
      case 'most-commented':
        return (b.comments?.length || 0) - (a.comments?.length || 0)
      default:
        return 0
    }
  })

  return result
})

// 分页数据
const totalFilteredPosts = computed(() => filteredPosts.value.length)
const paginatedPosts = computed(() => {
  const start = (currentPage.value - 1) * pageSize.value
  const end = start + pageSize.value
  return filteredPosts.value.slice(start, end)
})

// 处理搜索
const handleSearch = () => {
  currentPage.value = 1
}

// 处理筛选
const handleFilter = () => {
  currentPage.value = 1
}

// 处理排序
const handleSort = () => {
  currentPage.value = 1
}

// 清除筛选条件
const clearSearch = () => {
  searchQuery.value = ''
  handleSearch()
}

const clearCategory = () => {
  selectedCategory.value = ''
  handleFilter()
}

const clearAllFilters = () => {
  searchQuery.value = ''
  selectedCategory.value = ''
  sortBy.value = 'newest'
  currentPage.value = 1
}

// 处理分页
const handleSizeChange = (val) => {
  pageSize.value = val
  currentPage.value = 1
}

const handleCurrentChange = (val) => {
  currentPage.value = val
}

// 获取每个分类的文章数量
const getCategoryCount = (category) => {
  return allPosts.value.filter(post => post.category === category).length
}

// 示例文章标题列表
const exampleTitles = [
  '深入理解Vue3的响应式系统',
