const exampleCategories = ['前端', '后端', '工具', '架构', '教程']

// 添加示例文章的方法
const addExamplePost = () => {
  const titleIndex = allPosts.value.length % exampleTitles.length
  const tagsIndex = allPosts.value.length % exampleTags.length
  const categoryIndex = allPosts.value.length % exampleCategories.length

  const newPost = {
    id: allPosts.value.length + 1,
    title: exampleTitles[titleIndex],
    date: new Date().toISOString(),
    category: exampleCategories[categoryIndex],
    views: Math.floor(Math.random() * 2000) + 500, // 500-2500的随机阅读量
    likes: Math.floor(Math.random() * 100) + 10,   // 10-110的随机点赞数
    tags: exampleTags[tagsIndex],
    excerpt: `这是一篇关于${exampleTitles[titleIndex]}的详细教程。本文将从实际应用场景出发，深入浅出地讲解相关概念和实践方法，帮助读者更好地理解和应用这些技术。`,
    content: getExampleContent(exampleTitles[titleIndex])
  }

  // 添加到文章列表开头
  allPosts.value.unshift(newPost)
  
  // 重置分页到第一页
  currentPage.value = 1
  
  ElMessage({
