// 获取文章列表（不包含文章内容）
export const getPostsList = () => {
  return postsDatabase.map(({ content, comments, ...post }) => post)
}

// 根据ID获取文章详情
export const getPostById = (id) => {
  return postsDatabase.find(post => post.id === Number(id))
}

// 获取相邻文章
export const getAdjacentPosts = (id) => {
  const currentIndex = postsDatabase.findIndex(post => post.id === Number(id))
  return {
    prev: currentIndex > 0 ? postsDatabase[currentIndex - 1] : null,
    next: currentIndex < postsDatabase.length - 1 ? postsDatabase[currentIndex + 1] : null
  }
} 