<template>
  <div class="tag-list">
    <div class="tag-header">
      <h3>标签管理</h3>
      <el-button type="primary" size="small" @click="showAddTag = true">
        添加标签
      </el-button>
    </div>

    <div class="tags-container">
      <el-tag
        v-for="tag in tags"
        :key="tag.id"
        closable
        :type="tag.type"
        class="tag-item"
        @close="handleDelete(tag)"
      >
        {{ tag.name }}
        <span class="tag-count">({{ tag.count }})</span>
      </el-tag>
    </div>

    <el-dialog
      v-model="showAddTag"
      title="添加标签"
      width="30%"
      :close-on-click-modal="false"
    >
      <el-form :model="newTag" label-width="80px">
        <el-form-item label="标签名称">
          <el-input v-model="newTag.name" />
        </el-form-item>
        <el-form-item label="标签类型">
          <el-select v-model="newTag.type">
            <el-option label="默认" value="" />
            <el-option label="主要" value="primary" />
            <el-option label="成功" value="success" />
            <el-option label="警告" value="warning" />
            <el-option label="危险" value="danger" />
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="showAddTag = false">取消</el-button>
          <el-button type="primary" @click="handleAdd">确定</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref } from 'vue'

const props = defineProps({
  modelValue: {
    type: Array,
    default: () => []
  }
})

const emit = defineEmits(['update:modelValue', 'add', 'delete'])

const tags = ref(props.modelValue)
const showAddTag = ref(false)
const newTag = ref({
  name: '',
  type: '',
  count: 0
})

const handleAdd = () => {
  if (!newTag.value.name) return
  
  const tag = {
    id: Date.now(),
    name: newTag.value.name,
    type: newTag.value.type,
    count: 0
  }
  
  emit('add', tag)
  showAddTag.value = false
  newTag.value = { name: '', type: '', count: 0 }
}

const handleDelete = (tag) => {
  emit('delete', tag)
}
</script>

<style lang="scss" scoped>
.tag-list {
  .tag-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .tags-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    .tag-item {
      display: flex;
      align-items: center;
      
      .tag-count {
        margin-left: 5px;
        font-size: 0.8em;
        opacity: 0.8;
      }
    }
  }
}
</style> 