<script setup>
import { ref, computed } from 'vue'
import { ElMessage } from 'element-plus'
import { getPostsList } from '@/data/posts'

// 技能标签数据
const skills = [
  { name: 'Vue.js', type: 'success' },
  { name: 'React', type: 'primary' },
  { name: 'TypeScript', type: 'warning' },
  { name: 'Node.js', type: 'danger' },
  { name: 'Webpack', type: 'info' },
  { name: 'Git', type: 'info' },
  { name: 'CSS3', type: 'success' },
  { name: 'HTML5', type: 'warning' },
  { name: 'JavaScript', type: 'primary' },
  { name: 'Docker', type: 'info' }
]

// 个人经历数据
const experiences = [
  {
    id: 1,
    time: '2023 - 至今',
    title: '高级前端开发工程师 @ XX科技',
    description: '负责公司核心产品的前端架构设计和开发，带领团队完成多个重要项目。',
    type: 'primary'
  },
  {
    id: 2,
    time: '2021 - 2023',
    title: '前端开发工程师 @ YY公司',
    description: '参与电商平台的开发和维护，负责性能优化和用户体验改进。',
    type: 'success'
  },
  {
    id: 3,
    time: '2019 - 2021',
    title: '初级前端工程师 @ ZZ科技',
    description: '参与企业管理系统的开发，负责前端功能实现和页面开发。',
    type: 'info'
  }
]

// 博客统计数据
const blogStats = computed(() => {
  const posts = getPostsList()
  const totalViews = posts.reduce((sum, post) => sum + post.views, 0)
  const totalLikes = posts.reduce((sum, post) => sum + (post.likes || 0), 0)
  const categories = new Set(posts.map(post => post.category))
  const tags = new Set(posts.flatMap(post => post.tags))

  // 计算所有文章的评论总数
  const totalComments = posts.reduce((sum, post) => {
    // 从 localStorage 获取每篇文章的评论
    const comments = JSON.parse(
      localStorage.getItem(`post_${post.id}_comments`) || '[]'
    )
    return sum + comments.length
  }, 0)

  return [
    { 
      label: '文章数量', 
      value: posts.length,
      icon: 'el-icon-document'
    },
    { 
      label: '文章分类', 
      value: categories.size,
      icon: 'el-icon-folder'
    },
    { 
      label: '文章标签', 
      value: tags.size,
      icon: 'el-icon-collection-tag'
    },
    { 
      label: '总访问量', 
      value: totalViews,
      icon: 'el-icon-view'
    },
    { 
      label: '获得点赞', 
      value: totalLikes,
      icon: 'el-icon-star-off'
    },
    { 
      label: '评论数量', 
      value: totalComments,
      icon: 'el-icon-chat-dot-round'
    }
  ]
})

// QQ群数据
const qqGroups = [
  {
    name: '技术交流群①',
    number: '123456789',
    description: '前端技术交流，问题讨论，经验分享'
  },
  {
    name: '技术交流群②',
    number: '987654321',
    description: '后端技术交流，架构讨论，性能优化'
  },
  {
    name: '技术交流群③',
    number: '456789123',
    description: '全栈开发交流，项目经验分享'
  },
  {
    name: '技术交流群④',
    number: '789123456',
    description: '算法与数据结构交流，面试经验分享'
  }
]

// 复制群号功能
const copyGroupNumber = (number) => {
  navigator.clipboard.writeText(number)
    .then(() => {
      ElMessage.success('群号已复制到剪贴板')
    })
    .catch(() => {
      ElMessage.error('复制失败，请手动复制')
    })
}
</script>

